<template>
  <div class="discover">
    <div class="box" v-show="!pdfShow" @click="a(item)" v-for="(item,index) of arr" :key="index">{{item.con}}</div>
    <div v-show="pdfShow" class="iframe_box">
      <iframe
        :src=pdfSrc
        frameborder="0"
        scrolling="auto"
        class="iframe"
      ></iframe>
    </div>
    <img v-show="pdfShow" src="../assets/images/btn_close@1x.png" @click="pdfShow=false,pdfSrc=''" class="del" />
  </div>
</template>
<script>
export default {
  data() {
    return {
      pdfShow: false,
      pdfSrc:'',
      arr:[
        {con:"pdf1",src:"http://192.168.43.130:8080/hope3/static/upload/aa.pdf"},
        {con:"pdf2",src:"http://192.168.43.130:8080/hope3/static/upload/css.pdf"},
        {con:"pdf3",src:"http://192.168.43.130:8080/hope3/static/upload/js.pdf"},
        {con:"pdf4",src:"http://192.168.43.130:8080/hope3/static/upload/js1.pdf"},
      ]
    };
  },
  mounted() {},
  methods: {
    a(item){
      this.pdfShow=true
      this.pdfSrc = item.src
    }
  }
};
</script>
<style lang="less" scoped>
.discover {
  width: 100%;
  height: 100%;
  overflow: hidden;
  .box {
    width: 90%;
    height: .5rem;
    text-align: center;
    line-height: .5rem;
    margin: auto;
    border: 0.01rem solid #000;
    border-radius: 0.1rem;
    margin-top: .2rem;
  }
  .iframe_box {
    width: 100%;
    height: 85%;
    overflow: hidden;
    .iframe {
      width: 108%;
      height: 115%;
      margin-left: -0.05rem;
      margin-top: -0.6rem;
      margin-bottom: -0.1rem;
    }
  }
  .del {
    width: 0.5rem;
    height: 0.5rem;
    display: block;
    margin: auto;
  }
}
</style>
